<template>
  <div class="home-container">
    <!-- 导航栏 -->
    <van-nav-bar class="page-nav-bar" fixed>
      <van-button slot="title" type="info" size="small" round icon="search" class="search-btn" @click="moveSearchPage">搜索</van-button>
    </van-nav-bar>

    <!-- 频道列表 -->
    <van-tabs class="channel-tabs" v-model="active" animated swipeable>
      <van-tab :title="channel.name" v-for="channel in channels" :key="channel.id">
        <ArticleList :channel="channel" />
      </van-tab>
      <div slot="nav-right" class="placeholder"></div>
      <div slot="nav-right" class="hamburger-btn" @click="isChannelEditShow = true">
        <i class="toutiao toutiao-gengduo"></i>
      </div>
    </van-tabs>
    <!-- 频道编辑弹出层 -->
    <van-popup v-model="isChannelEditShow" round closeable position="bottom" :style="{ height: '87%' }" lazy-render @close="closeFn">
      <ChannelEdit :myChannels="channels" :active="active" @addChannels="addChannels" @updateChannel="updateChannel" @delChannel="delChannel" ref="editRef" />
    </van-popup>
  </div>
</template>

<script>
import { getUserChannels } from '@/api/user'
import { updateChannels, removedChannel } from '@/api/channel'
import ArticleList from './components/article-list'
import ChannelEdit from './components/channel-edit'
export default {
  name: 'HomeIndex',
  data() {
    return {
      active: 0,
      // 频道列表
      channels: [],
      // 控制编辑弹出层展示状态
      isChannelEditShow: false
    }
  },
  methods: {
    async loadChannels() {
      try {
        const { data: res } = await getUserChannels()
        // console.log(res)
        this.channels = res.data.channels
      } catch (err) {
        this.$toast('获取频道列表数据失败')
      }
    },
    // 添加用户频道
    async addChannels(val) {
      // console.log(this.channels)
      // 推荐频道不要传递
      // const newArr = this.channels.filter((item) => item.id !== 0)
      /* newArr.forEach((item, index) => {
        delete item.name // 删除对象name属性
        item.seq = index
      }) */

      // 新增时，名字被删除，所有数组里的对象都是同一个地址
      // const newArr = this.channels.filter((item) => item.id !== 0)
      //       console.log(newArr)
      //       const { data: res } = await updateChannels(newArr)
      //       console.log(res)

      // 解决方案 不影响原数组
      const newArr = this.channels.filter((item) => item.id !== 0)
      // console.log(newArr)
      const theNewArr = newArr.map((item, index) => {
        const obj = { ...item }
        delete obj.name
        obj.seq = index
        return obj
      })
      // console.log(theNewArr)
      await updateChannels(theNewArr)
      this.channels.push(val)
      // console.log(res)
    },
    // 切换频道
    updateChannel(index) {
      this.active = index // 修改当前频道项
      this.isChannelEditShow = false // 关闭弹层
    },
    // 删除频道
    async delChannel(obj) {
      const index = this.channels.findIndex((item) => obj.id === item.id)
      this.channels.splice(index, 1)
      await removedChannel(obj.id)
      // console.log(res) 删除接口返回状态码204，返回无内容
    },
    // 关闭弹出层
    closeFn() {
      this.$refs.editRef.isEdit = false
    },
    // 跳转搜索页面
    moveSearchPage() {
      this.$router.push('/search')
    }
  },
  created() {
    this.loadChannels()
  },
  components: { ArticleList, ChannelEdit }
}
</script>

<style lang="less" scoped>
.home-container {
  padding: 174px 0 100px 0;
  /deep/.van-nav-bar__title {
    max-width: unset;
    .search-btn {
      width: 555px;
      height: 64px;
      background-color: #5babfb;
      border: none;
      font-size: 28px;
      .van-icon-search:before {
        color: #fff;
        font-size: 40px;
      }
    }
  }
  /deep/.van-tabs__wrap {
    height: 82px;
    position: fixed;
    top: 92px;
    // 左右撑开
    left: 0;
    right: 0;
    z-index: 999;
  }
  /deep/.channel-tabs {
    .van-tabs__nav {
      padding: 0;
    }
    .van-tab {
      border-right: 1px solid #edeff3;
      // 最小宽度
      min-width: 220px;
      flex: 22%;
      font-size: 30px;
      color: #777;
    }
    .van-tab--active {
      color: #333;
    }
    .van-tabs__line {
      bottom: 8px;
      width: 31px;
      height: 6px;
      background-color: #3296fa;
    }
    .placeholder {
      width: 66px;
      // 不参与计算
      flex-shrink: 0;
      height: 82px;
    }
    .hamburger-btn {
      width: 66px;
      height: 82px;
      position: fixed;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      opacity: 0.902;
      i.toutiao {
        font-size: 33px;
      }
      &:before {
        content: '';
        position: absolute;
        left: 0;
        width: 1px;
        height: 100%;
        background-image: url('@/assets/gradient-gray-line.png');
        background-size: contain;
      }
    }
  }
  .van-popup--bottom.van-popup--round {
    box-sizing: border-box;
    padding: 85px 0;
  }
}
</style>
