<template>
  <div class="channel-edit">
    <!-- 我的频道 -->
    <van-cell :border="false">
      <div slot="title" class="title-text">我的频道</div>
      <van-button type="danger" plain round size="mini" class="edit-btn" @click="isEdit = !isEdit">{{ isEdit ? '完成' : '编辑' }}</van-button>
    </van-cell>
    <van-grid :column-num="4" :gutter="10" class="my-grid">
      <van-grid-item v-for="(value, index) in myChannels" :key="index" class="grid-item" @click="onMychannelClick(value, index)">
        <van-icon slot="icon" name="clear" v-show="isEdit && !fiexChannels.includes(value.id)"></van-icon>
        <span slot="text" class="text" :class="{ active: index === active }">{{ value.name }}</span>
      </van-grid-item>
    </van-grid>

    <!-- 推荐频道 -->
    <van-cell :border="false">
      <div slot="title" class="title-text">推荐频道</div>
    </van-cell>
    <van-grid :column-num="4" :gutter="10" class="recommend-grid">
      <van-grid-item icon="plus" v-for="(value, index) in recommendChannels" :key="index" :text="value.name" class="grid-item" @click="onAddChannel(value)" />
    </van-grid>
  </div>
</template>

<script>
import { getAllChannels } from '@/api/channel'
export default {
  name: 'ChannelEdit',
  props: {
    myChannels: {
      type: Array,
      required: true
    },
    active: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      allChannels: [],
      isEdit: false, // 控制编辑状态显示
      fiexChannels: [0] // 固定频道无法删除
    }
  },
  created() {
    this.loadAllChannels()
    // console.log(this.myChannels)
  },
  methods: {
    async loadAllChannels() {
      try {
        const { data: res } = await getAllChannels()
        // console.log(res)
        this.allChannels = res.data.channels
      } catch (err) {
        console.log(err)
      }
    },
    onAddChannel(val) {
      // console.log(val)
      this.$emit('addChannels', val)
    },
    // 编辑 非编辑
    onMychannelClick(channel, index) {
      if (this.isEdit) {
        if (this.fiexChannels.includes(channel.id)) {
          this.$toast('推荐无法删除')
        } else {
          // 编辑状态删除
          this.$emit('delChannel', channel)
        }
      } else {
        // 非编辑不删除跳转
        this.$emit('updateChannel', index)
      }
    }
  },
  computed: {
    recommendChannels() {
      // 过滤筛选
      /* const arr = this.allChannels.filter((item) => {
        const index = this.myChannels.findIndex((val) => val.id === item.id)
        if (index > -1) {
          return false
        } else {
          return true
        }
      })
      return arr */
      return this.allChannels.filter((item) => this.myChannels.findIndex((val) => val.id === item.id) === -1)
    }
  }
}
</script>

<style lang="less" scoped>
.channel-edit {
  .title-text {
    font-size: 32px;
    color: #333;
  }
  .edit-btn {
    width: 104px;
    height: 48px;
    font-size: 26px;
    color: #f85959;
    border: 1px solid;
  }
  /deep/.grid-item {
    height: 86px;
    width: 160px;
    justify-content: center;
    .van-grid-item__content {
      white-space: nowrap;
      background-color: #f4f5f6;
      width: 160px;
      margin: 0;
      .van-grid-item__text,
      .text {
        font-size: 28px;
        color: #222;
      }
      .active {
        color: #f85959;
      }
    }
  }
  /deep/.recommend-grid {
    .grid-item {
      .van-grid-item__content {
        flex-direction: row;
        .van-grid-item__text {
          line-height: 0;
          margin: 0;
        }
        .van-grid-item__icon {
          font-size: 28px;
          margin-right: 3px;
          margin-bottom: 2px;
        }
      }
    }
  }
  /deep/.my-grid {
    .van-grid-item__icon-wrapper {
      position: unset;
    }
    .van-grid-item__text {
      line-height: 0;
      margin: 0;
    }
    .van-grid-item__icon-wrapper {
      position: absolute;
      right: -5px;
      top: -5px;
      font-size: 25px;
      color: #cacaca;
      z-index: 2;
    }
  }
}
</style>
