<template>
  <div class="articlelist-container">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :success-text="refreshSuccess" :success-duration="1000">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" :error.sync="error" error-text="请求失败，点击重新加载" @load="onLoad">
        <!-- 自己封装组件无法理解click，需加修饰符native绑定原生click事件（任何原生事件都可用）反馈面板会有事件冒泡，需阻止 -->
        <ArticleItem v-for="val in list" :key="val.art_id" :article="val" :onRefresh="onRefresh" :hide="true" @click.native="detailFn(val.art_id)" />
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticles } from '@/api/article'
import ArticleItem from '@/components/article-item'

export default {
  name: 'ArticleList',
  components: { ArticleItem },
  props: {
    channel: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      list: [], // 存储列表数据的数组
      loading: false, // 控制加载中loading状态
      finished: false, // 控制数据加载结束状态
      timestamp: null, // 请求获取下一页时间戳
      error: false, // 控制失败提示状态
      isLoading: false, // 控制下拉刷新的loading状态
      refreshSuccess: '刷新成功' // 下拉刷新成功提示文本
    }
  },
  // 数据请求异步,在挂载之前没请求回来
  // 网页刚打开就会执行onload，触底也会执行，不需再写created，不然key重复
  // 解决方案：list组件添加属性初始化时不执行，函数内判定数组长度
  methods: {
    // 初始化或者滚动到底部时会触发
    async onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      try {
        const { data: res } = await getArticles({
          channel_id: this.channel.id,
          // 简单理解是请求数据的页码类似
          // 第一页：当前最新时间戳
          // 请求之后数据时间戳会在当前请求结果中返回
          timestamp: this.timestamp || Date.now()
        })

        // 模拟失败情况
        /* if (Math.random() > 0.5) {
          JSON.parse('dfcc ')
        } */
        // console.log(res)

        // 展开运算符。不然就数组里面套数组，是不对的
        this.list.push(...res.data.results)
        // 加载状态结束 loading关闭以后，才能触发下一次加载更多
        this.loading = false
        // 数据全部加载完成 如果本次还有数据
        if (res.data.pre_timestamp) {
          this.timestamp = res.data.pre_timestamp
        } else {
          // 没有数据了，设置为true，不再加载更多
          this.finished = true
        }
      } catch (err) {
        this.$toast('加载文章失败，稍后重试')
        // 展示错误状态
        this.error = true
        // 失败后加载状态也要关闭
        this.loading = false
        // console.log(err)
      }
      // 结果数据放入list
      /*  for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1)
        } */

      // 加载状态结束 loading关闭以后，才能触发下一次加载更多
      // this.loading = false

      // 数据全部加载完成
      /* if (this.list.length >= 40) {
          // 没有数据了，设置为true，之后不再触发加载更多
          this.finished = true
        }
      }, 1000) */
    },
    async onRefresh() {
      // console.log('刷刷新')
      this.timestamp = Date.now()
      // 请求获取数据
      try {
        const { data: res } = await getArticles({
          channel_id: this.channel.id,
          // 简单理解是请求数据的页码类似
          // 第一页：当前最新时间戳
          // 请求之后数据时间戳会在当前请求结果中返回
          timestamp: this.timestamp
        })
        this.list = []
        this.list = [...res.data.results]
        this.refreshSuccess = '刷新成功'
        this.timestamp = res.data.pre_timestamp
        this.isLoading = false
      } catch (err) {
        this.isLoading = false
        this.refreshSuccess = '刷新失败'
      }
      // 将数据追加到列表顶部
      // 关闭刷新状态
    },
    // 跳转文章详情
    detailFn(id) {
      // console.log(id)
      this.$router.push({
        path: '/article_detail',
        query: {
          art_id: id
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
// 设置滚动容器，互不影响，不然就是body滚动，大家都是一个body，互相影响
.articlelist-container {
  // 百分比单位相对于父元素
  height: 81vh;
  overflow-y: auto;
}
</style>
