<template>
  <div>
    <!-- <van-cell @click="detailFn" clickable> -->
    <van-cell clickable>
      <!-- 文章表题 -->
      <div slot="title" class="title van-multi-ellipsis--l2">{{ article.title }}</div>
      <div slot="label">
        <div class="cover-wrap" v-if="article.cover.type === 3">
          <van-image v-for="(img, index) in article.cover.images" fit="cover" :src="img" :key="index" lazy-load>
            <van-loading slot="loading" type="spinner" size="20" vertical />
            <!-- 404图片地址失效，换个提示 -->
            <text slot="error">加载失败</text>
          </van-image>
        </div>
        <div class="lable-info-wrap" :class="{ hide: !hide }">
          <span>{{ article.aut_name }}</span>
          <span>{{ article.comm_count }}</span>
          <span>{{ article.pubdate | relativeTime }}</span>
        </div>
      </div>
      <van-image class="right-cover" v-if="article.cover.type === 1" fit="cover" :src="article.cover.images[0]" slot="default" lazy-load>
        <van-loading slot="loading" type="spinner" size="20" vertical />
        <text slot="error">加载失败</text>
      </van-image>
      <div slot="right-icon" class="toutiao toutiao-guanbi" @click.stop="show = true" v-if="hide"></div>
    </van-cell>
    <!-- 反馈面板 -->
    <van-action-sheet @cancel="cancelFn" v-model="show" :actions="actions" @select="onSelect" get-container="body" :cancel-text="bottomText" lazy-render @close="closeFn" />
  </div>
</template>

<script>
// 点击反馈内容数据的切换
// 监听点击事件，区分点击哪一个
// 切换数据
// 二级反馈取消按钮文字变为返回
// 给cancel-text设置一个变量，把变量值变为返回
// 点击按钮区分判断是否为返回，如果是，把actions数据源替换为firstActions,然后由数据驱动视图发生改变，返回变为取消，然后show=true可以看见
import { firstActions, secondActions } from '@/api/report'

// 接口请求
import { UnlikeArticles, FeedbackArticles } from '@/api/article'
export default {
  name: 'ArticleItem',
  props: {
    article: {
      type: Object,
      required: true
    },
    onRefresh: Function,
    hide: Boolean
  },
  data() {
    return {
      show: false, // 反馈面板显示或隐藏
      actions: firstActions,
      bottomText: '取消' // 底部按钮的文字
    }
  },
  methods: {
    async onSelect(item) {
      // action // {名字}
      // item // 索引值
      // console.log(item)
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      // this.show = false
      // console.log(this.article)
      if (item.name === '反馈垃圾内容') {
        this.actions = secondActions
        this.bottomText = '返回'
      } else if (item.name === '不感兴趣') {
        // 不感兴趣
        // try catch 自己处理错误，内部throw就不会向控制台抛出打印错误，而是交给catch自定义
        // try catch只捕获同步代码的异常
        // await等待异步任务执行结果才往下走
        try {
          await UnlikeArticles(this.article.art_id)
          this.$toast('将为您减少此类推荐')
          this.onRefresh()
          // console.log(res)
        } catch (err) {}
        this.show = false
      } else {
        // 反馈垃圾内容
        try {
          await FeedbackArticles({
            target: this.article.art_id,
            type: item.value,
            remark: '无'
          })
          this.$toast('感谢您的反馈')
          this.show = false
          this.onRefresh()
          // console.log(res)
        } catch (err) {
          // this.$toast('操作失败，稍后再试')
        }
      }
    },
    // 反馈面板
    cancelFn() {
      if (this.bottomText === '返回') {
        this.show = true // 强制不退出
        this.actions = firstActions
        this.bottomText = '取消'
      }
    },
    // 关闭面板触发事件
    closeFn() {
      // 方法一
      this.actions = firstActions
      this.bottomText = '取消'
    }
    // 点击跳转详情,如果不止这个组件要用，那可以把点击跳转路由的功能设置于其父组件
    // detailFn() {
    /*   console.log(this.article.art_id)
    } */
  }
  /* watch: {
    // 方法二
    /* show(val) {
      if (val === true) {
        this.actions = firstActions
        this.bottomText = '取消'
      }
    } */
}
</script>

<style lang="less" scoped>
.title {
  font-size: 32px;
  color: #3a3a3a;
  margin-bottom: 77px;
  position: relative;
}
.van-cell__value {
  flex: unset;
  width: 232px;
  height: 146px;
  padding-left: 25px;
}
.right-cover {
  width: 232px;
  height: 130px;
  margin-bottom: 51px;
}
.lable-info-wrap {
  position: absolute;
  left: 100px;
  bottom: 20px;
  span {
    font-size: 22px;
    color: #b4b4b4;
    margin-right: 25px;
  }
}
.hide {
  left: 34px;
}
.cover-wrap {
  display: flex;
  position: relative;
  top: -58px;
  .van-image {
    flex: 1;
    height: 146px;
    &:not(:last-child) {
      padding-right: 4px;
    }
    .van-image__img {
      width: 100%;
      height: 146px;
    }
  }
}
.toutiao {
  box-sizing: border-box;
  position: absolute;
  width: 23px;
  height: 23px;
  left: 35px;
  bottom: 26px;
  font-size: 15px;
  line-height: 23px;
  text-align: center;
  color: #d0d0d0;
  border: 2px solid #c9c9c9;
  border-radius: 13px;
  // font-weight: 600;
  overflow: hidden;
}
</style>
