// 文章请求模块
import request from '@/utils/request'
// 请求获取文章列表
export const getArticles = (params) => {
  return request.get('/v1_0/articles', {
    params
  })
}
// 文章反馈-不感兴趣
export const UnlikeArticles = (target) => {
  return request.post('/v1_0/article/dislikes', {
    target
  })
}
// 文章反馈-反馈
export const FeedbackArticles = (obj) => {
  return request.post('/v1_0/article/reports', obj)
}
// 文章详情
export const articleDetail = (id) => {
  return request.get(`/v1_0/articles/${id}`)
}
// 对文章点赞
export const likeArticle = (target) => {
  return request.post('/v1_0/article/likings', { target })
}
// 对文章取消点赞
export const disLikeArticle = (target) => {
  return request.delete(`/v1_0/article/likings/${target}`)
}
// 文章评论列表
export const commentsList = ({ id, offset = null, limit = 10 }) => {
  return request.get('/v1_0/comments', {
    params: {
      // axios针对param参数，如果发现键值对的值为null，会忽略此参数名和值不携带
      type: 'a',
      source: id,
      offset,
      limit
    }
  })
}
// 发表评论
export const commentsAdd = ({ id, content, artId = null }) => {
  // axios请求体传参如果值为null是不会忽略这个参数的也会把参数名和值携带给后台（只有params遇到null才会忽略）
  // 形参是art_id可选参数，如果逻辑页面是对文章评论，则不需要传，所以这是，内部art_id值为null就证明此次调用是针对文章评论
  const obj = {
    target: id,
    content,
    art_id: artId
  }
  if (!artId) {
    obj.art_id = artId
  }
  return request.post('/v1_0/comments', obj)
}
// 评论点赞
export const likeComments = (target) => {
  return request.post('/v1_0/comment/likings', { target })
}
// 取消点赞
export const disLikeComments = (target) => {
  return request.delete(`/v1_0/comment/likings/${target}`)
}
