import request from '@/utils/request'
// 获取所有频道

export const getAllChannels = () => request.get('/v1_0/channels')

// 添加频道
export const updateChannels = (channels) =>
  request({
    method: 'PUT',
    url: '/v1_0/user/channels',
    data: {
      channels
    }
  })
// 删除频道
export const removedChannel = (id) =>
  request({
    method: 'DELETE',
    url: `/v1_0/user/channels/${id}`
  })
